import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Form, message } from 'antd';
import { TableDataEditRow, SelectAjax, CartHeadAction } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import {
  getClassResponsibles,
  createClassResponsible,
} from 'services/classResponsible';
import { getStudentTitles } from 'services/studentTitle';
import { BtnExport } from 'components/common/button';
import SearchBar from './SearchBar';
import ModalExportClassOfficer from './ModalExportClassOfficer';
import './style.scss';

class ListClassOfficers extends Component {
  state = {
    reload: 0,
    paramSearch: {},
    showExportModal: false,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  columns = [
    {
      title: 'Mã SV',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: 'Họ tên',
      dataIndex: 'fullName',
      key: 'fullName',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Chức danh',
      dataIndex: 'studentTitle',
      key: 'studentTitle',
      editable: true,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('studentTitleId', {
            initialValue: record.studentTitleId,
          })(<SelectAjax form={form} service={getStudentTitles} allowGetObjSelected />)}
        </Form.Item>
      ),
    },
    {
      title: 'Chức danh kim',
      dataIndex: 'studentSecondTitle',
      key: 'studentSecondTitle',
      editable: true,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('studentSecondTitleId', {
            initialValue: record.studentSecondTitleId,
          })(<SelectAjax form={form} service={getStudentTitles} allowGetObjSelected />)}
        </Form.Item>
      ),
    },
  ];

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleSaveRowData = async rowData => {
    const { objSelected, studentId, classId, studentTitleId, studentSecondTitleId } = rowData;
    const dataReq = {
      studentId,
      classId,
      studentTitles: [studentTitleId, studentSecondTitleId].filter(item => item),
    };
    if (!studentTitleId && studentSecondTitleId) {
      message.error('Vui lòng chọn chức danh chính');
      return null;
    }
    if (studentTitleId && studentTitleId === studentSecondTitleId) {
      message.error('Vui lòng chọn hai chức danh khác nhau');
      return null;
    }
    try {
      await createClassResponsible(dataReq);
      return {
        ...rowData,
        studentTitle: objSelected.studentTitleId.label,
        studentSecondTitle: objSelected.studentSecondTitleId.label,
      };
    } catch (e) {
      return null;
    }
  };

  handleExport = async () => {
    this.setState({
      showExportModal: true,
    });
  };

  handleOKExportModal = () => {
    this.setState({
      reload: this.state.reload + 1,
      showExportModal: false,
    });
  }

  handleCancelExportModal = () => {
    this.setState({
      showExportModal: false,
    });
  }

  render() {
    return (
      <Fragment>
        <Card>
          <CartHeadAction>
            <BtnExport onClick={this.handleExport} />
          </CartHeadAction>
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableDataEditRow
                reload={this.state.reload}
                paramSearch={this.state.paramSearch}
                service={getClassResponsibles}
                columns={this.columns}
                handleSaveRowData={this.handleSaveRowData}
                rowKey="studentId"
              />
            </Col>
          </Row>
        </Card>
        <ModalExportClassOfficer
          visible={this.state.showExportModal}
          onSuccess={this.handleOKExportModal}
          onCancel={this.handleCancelExportModal}
        />
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListClassOfficers />
  </Page>
);



