import React, { Component, Fragment } from 'react';
import { 
  Row, 
  Col, 
  Form, 
  Modal,
  Spin,
} from 'antd';
import { 
  SelectAjax, 
} from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getCourses } from 'services/course';
import {
  exportClassResponsibles,
} from 'services/classResponsible';

@Form.create({ name: 'ModalCopyProgram' })
class ModalExportClassOfficer extends Component {
  state = {
    loading: false,
  };

  handleSave = () => {
    this.props.form.validateFields(async(err, values) => {
      if(err) {
        return;
      }
      try {
        this.setState({
          loading: true,
        });
        await exportClassResponsibles(values);
        this.props.onSuccess();
      } catch (error) {
        console.log(error);
      } finally {
        this.setState({
          loading: false,
        });
      }
    });
  }

  render() {
    const { loading } = this.state;
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const layout = {
      labelCol: { span: 8 },
      wrapperCol: { span: 16 },
    };
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
    };
    return (
      <Modal
        title='Xuất cán bộ lớp'
        visible={this.props.visible}
        onOk={this.handleSave}
        okText="Xuất"
        onCancel={this.props.onCancel}
        cancelText="Hủy"
        width={550}
      >
        <Spin spinning={loading}>
          <Form {...layout}>
            <Fragment>
              <Row gutter={24}>
                <Col span={20} offset={2}> 
                  <Form.Item {...formItemLayout} label="Hệ">
                    {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(
                      <SelectAjax 
                        service={getTrainTypes} 
                      />
                    )}
                  </Form.Item>
                  <Form.Item {...formItemLayout} label="Khoa">
                    {getFieldDecorator('facultyId', optionFieldDecorator.facultyId)(
                      <SelectAjax
                        setFieldsValue={setFieldsValue}
                        service={getFacultys}
                        params={{ trainTypeId }}
                        allowGetData={!!trainTypeId}
                      />
                    )}
                  </Form.Item>
                  <Form.Item {...formItemLayout} label="Khoá">
                    {getFieldDecorator('courseId', optionFieldDecorator.courseId)(
                      <SelectAjax
                        setFieldsValue={setFieldsValue}
                        service={getCourses}
                        params={{ trainTypeId, facultyId }}
                        allowGetData={!!facultyId}
                      />
                    )}
                  </Form.Item>
                </Col>
              </Row>
            </Fragment>
          </Form>
        </Spin>
      </Modal>
    );
  }
}

export default ModalExportClassOfficer;



