import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getClassResponsibles = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/ClassResponsible',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createClassResponsible = ({ studentId, classId, studentTitles } = {}) =>
  request({
    prefix,

    url: '/ClassResponsible',
    method: 'POST',
    params: {},
    data: { studentId, classId, studentTitles },
  });

export const exportClassResponsibles = ({
  trainTypeId,
  facultyId,
  courseId,
} = {}) =>
  requestDownload({
    prefix,
    defaultFileName: 'Exported File.xlsx',
    url: '/ClassResponsible/ExportClassResponsibles',
    method: 'POST',
    params: {},
    data: { trainTypeId, facultyId, courseId },
  });



